<template>
    <div class="box">
        <Header :got="true" :gnt="{name:mage.name}" :gmt="false" :cot="true"></Header>
        <div class="content">
            <div class="item">
                <el-input v-model="input" placeholder="输入学校，商务楼，地址" class="inp" clearable> ></el-input>
                <el-button type="primary" @click="search()" class="but">提交</el-button>
            </div>
            <div class="history" v-if="this.list.length<=0">
                <div class="search">
                    搜索历史
                </div>
                <div class="region" v-for="(item,index) in info" :key="index" @click="getParams(item)">
                    <p><b>{{item.name}}</b></p>
                    <p>{{item.address}}</p>
                </div>
                <el-button type="primary" @click="del()" class="prim">清空所有</el-button>
            </div>
            <div class="history" v-else>
                <div class="region" v-for="(item,index) in list" :key="index" @click="getParams(item)">
                    <p><b>{{item.name}}</b></p>
                    <p>{{item.address}}</p>
                </div>
            </div>
            <div class="history" v-if="this.list.length>1">
                搜索内容不存在
            </div>
        </div>
    </div>
</template>

<script>
import Header from '../page/header/Header'
    export default {
        data() {
            return {
                 input: '',
                 list:[],
                 mage:'',
                 info:[],
                 geohash:''

            }
        },
        components:{
            Header
        },
        methods:{
            search(){
                if(this.input){
                    this.http.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.params.id+'&keyword='+this.input).then((res)=>{
                        // console.log(res.data)
                        this.list=res.data;
                    }) 
                       
                }   
            },
            getParams(item){
                this.$router.push({path:`/home?geohash=${item.geohash}`})
                localStorage.setItem('geohash', JSON.stringify(this.$router.history.current.query.geohash))
                let obj = [...this.info]
                let zhi = obj.some((v)=>{
                    return v.geohash == item.geohash
                })
                if(zhi == false){
                    obj.push(item)
                }
                localStorage.setItem('list',JSON.stringify(obj))
                this.info = obj
            },
            del(){
                this.info=[];
                localStorage.removeItem('list');
            },
            
        },
        mounted () {
            let obj=JSON.parse(localStorage.getItem('list'))
            if(obj !=null){
                this.info = obj
            }
            this.http.get(`http://elm.cangdu.org/v1/cities/${this.$route.params.id}`).then((res)=>{
                this.mage=res.data
            })
        }
    }
</script>

<style lang="scss" scoped>
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-size: 0.35rem;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        font-size: 0.35rem;
        .item{
            width: 100%;
            height: 2.5rem;border-bottom: 0.001rem solid rgb(228, 206, 206);
            .inp{
                width: 90%;
                margin: 0.3rem 5%;
            }
            .but{
                width: 90%;
                margin: 0.1rem 5%;
            }
        }
        .history{
            width: 100%;
            .search{
                width: 100%;
                height: 1rem;
                line-height: 1rem;
                padding: 0 0.5rem;
                border-bottom: 0.001rem solid rgb(228, 206, 206); 
            }
        }
        .region{
            width: 100%;
            height: 1.8rem;
            border-bottom: 0.001rem solid rgb(228, 206, 206); 
            // padding: 0.25rem 0.5rem;
            // margin: 0.5rem;
            // margin: 0.1rem;
            margin-top: 0.1rem;
            a{
                text-decoration: none;
                color: rgb(126, 107, 107);
            }
            p{
                padding: 0.2rem 0.5rem;
                font-size: 0.3rem;
            }
            p:nth-of-type(2){
                font-size: 0.25rem;
                color: rgb(228, 206, 206);
            }
        }
        .prim{
            width: 100%;
            border: none;
            height: 1rem;
            background:whitesmoke;
            color: rgb(196, 165, 165);
        }
    }
</style>


